export default {
	props:{
		item:Object,
		index:Number,
		tool:Array,
		jl:Array
	},
	data() {
		return {
			state:[
				"平台审核中",
				"审核通过",
				"审核失败"
			],
			index1:0,
			itemData:[]
		}
	},
	onReady() {
		this.label = this.item.label.split(/[ ]+/);
		console.log("标签");
		console.log(this.label);
	},
	methods:{
		goDetail: function(item) {
			let detail = {
				Distance: item.Distance,
				budget: item.budget,
				code: item.code,
				content: item.content,
				delete: item.delete,
				destination: item.destination,
				door_time: item.door_time,
				duration: item.duration,
				label: item.label,
				longitude: item.longitude,
				name: item.name,
				origin: item.origin,
				send: item.send,
				state: item.state,
				tel: item.tel,
				type: item.type,
				uid: item.uid
			};
			uni.navigateTo({
				url: '../../pages/order-detail/order-detail?detailDate=' + encodeURIComponent(JSON.stringify(detail))
			});
		},
	}
}
